@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap')

body
 background-color: hsl(212, 45%, 89%)

.container
    width:100%
    height:1080px
    /*Center vertically and horizontally*/
    //display: flex
    //justify-content: center
    //align-items: center

.card-container
    background-color: hsl(0, 0%, 100%)
    width: 300px
    overflow: hidden
    margin: auto !important 
    margin-top: 12% !important
    //box-shadow: 0px 0px 15px -5px
    transition:0.3s
    animation: ease-in-out
    padding:1rem
    border-radius:4%

    

.card-container:hover
    transform: scale(1.1)
    cursor: pointer
    //box-shadow:0px 0px 15px 0px


.image-container img
    overflow: hidden
    width:100%
    

.card-content
    margin: 1rem
    margin-top: 0.5rem

h3
    font-family: 'Outfit', sans-serif
    font-size: 28px
    font-weight: 700
    color: hsl(218, 44%, 22%)

p
    margin:0
    padding:0
    font-size: 1.22em
    font-family: 'Outfit', sans-serif
    font-weight: 400 
    color: hsl(220, 15%, 55%)
    margin-bottom: 2rem

a:link 
    text-decoration: none

a:visited 
    text-decoration: none

a:hover 
    text-decoration: none

a:active 
    text-decoration: none

.card-title
    margin-bottom: 0.5rem